.allarea {
  height: 140px;
  color: rgb(63, 72, 101);
  display: grid;

  grid-template-columns: 1fr 1fr 3fr;

  grid-template-rows: 6fr 1fr 3fr;
  grid-template-areas:
    "title title gauge"
    "titlac titltr gauge"
    "actual target percent";
}
.dalam {
  margin: auto;
  text-align: center;
  /* border-radius: 1em; */
}
.dalamtitle {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 0.3em;

  /* border-radius: 1em; */
}
.gauge {
  display: grid;
  place-content: center;
  border-top: solid;
  border-width: 0.5px solid black;
  height: 100%;
  grid-area: gauge;
  color: var(--dark-text);
  background-color: transparent;
}
.title {
  border: solid;
  border-width: 0.5px;
  font-size: 20px;
  grid-area: title;
  display: grid;
  color: black;
  background-color: white;
}
.percent {
  border-bottom: solid;
  border-width: 0.5px;
  font-size: 20px;
  display: grid;
  grid-area: percent;
  color: black;
  background-color: white;
}
.actual {
  border: solid;
  border-width: 0.5px;

  font-size: 20px;
  display: grid;
  grid-area: actual;
  color: black;
  background-color: rgb(218, 179, 145);
}
.target {
  border: solid;
  border-width: 0.5px;
  font-size: 20px;
  display: grid;
  grid-area: target;
  color: black;
  background-color: rgb(218, 179, 145);
}
.titlac {
  border: solid;
  border-width: 0.5px;
  display: grid;
  grid-area: titlac;
  color: black;
  background-color: rgb(218, 179, 145);
}
.titltr {
  border: solid;
  border-width: 0.5px;
  display: grid;
  grid-area: titltr;
  color: black;
  background-color: rgb(218, 179, 145);
}
